.board-wrap {
  height: 100%;
  overflow-y: hidden;
  /* overflow-x: hidden; */
}
.board-wrap:hover {
  overflow-y: scroll;
}
.head-item {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 40px;
  box-shadow: 0 0 8px 4px #f5f5f5;
  padding: 24px;
  border-radius: 8px;
}

.head-item h3 {
  margin-left: 24px;
  font-size: 18px;
}
.total {
  margin-top: 60px;
  padding: 24px;
  height: 500px;
  box-shadow: 0 0 8px 4px #f5f5f5;
  border-radius: 8px;
}
.total-comment,
.month-total,
.total-comment .ant-card-body,
.month-total .ant-card-body {
  height: 300px;
}
